<template>
    <div>
        <b-row>
            <b-col cols="12" lg="12">
                <div class="box">
                    <div class="d-block">
                        <b-row>
                            <b-col cols="12" lg="6" md="6">
                                <label class="sorting">
                                    Tampilkan
                                    <select v-model="perPage" class="per-page" id="">
                                        <option value="10">10</option>
                                        <option value="50">50</option>
                                        <option value="100">100</option>
                                    </select>
                                    Data
                                </label>
                            </b-col>
                            <b-col cols="12" offset="0" lg="4" md="6" offset-lg="2">
                                <b-form-input
                                    class="float-right"
                                    v-model="filterDebounced"
                                    placeholder="Masukkan kata kunci..."/>
                            </b-col>
                        </b-row>

                        <div class="position-relative mt-4">
                            <b-table responsive
                                class="main-table"
                                ref="tabel"
                                :busy.sync="isBusy"
                                :items="providerTable"
                                :fields="tableColumns"
                                :current-page="currentPage"
                                :per-page="perPage"
                                :filter="filter"
                                :sort-by.sync="sortBy"
                                :sort-desc.sync="sortDesc"
                                @row-clicked="detail">
                                <template v-slot:cell(index)="data">
                                    {{ ((currentPage - 1) * perPage) + data.index + 1 }}
                                </template>
                            </b-table>
                            <div class="loading" v-show="isBusy">
                                <spinner :speed=".4" :size="30" />
                            </div>
                        </div>

                        <b-row>
                            <b-col cols="12" lg="4" class="text-center-sm">
                                Menampilkan {{ firstRows }} ke {{ viewRows }} dari {{ totalRows }} data
                            </b-col>
                            <b-col cols="12" lg="8" class="text-center-sm">
                                <b-pagination
                                    class="float-right"
                                    :total-rows="totalRows"
                                    v-model="currentPage"
                                    :per-page="perPage" />
                            </b-col>
                        </b-row>
                    </div>
                </div>
            </b-col>
        </b-row>
    </div>
</template>
<script>
    import { debounce } from 'lodash'
    import spinner from 'vue-simple-spinner'
    export default {
        name: 'dispenduk-data-almarhum',
        components: {
            spinner
        },
        data() {
            return {
                totalRows: 100,
                viewRows: 10,
                perPage: 10,
                currentPage: 1,
                filter: '',
                filterDebounced: '',
                isBusy: false,
                sortBy: 'id',
                sortDesc: true,
                tableColumns: [
                    { key: 'index', label: 'No', tdClass: 'number p-lg-3' },
                    { key: 'nik', label: 'NIK', tdClass: 'main-text' },
                    { key: 'nama', label: 'Nama', tdClass: 'sub-text' },
                    { key: 'alamat', label: 'Alamat', tdClass: 'description' },
                ]
            }
        },
        computed: {
            firstRows() {
                let ceil = (this.currentPage * this.perPage) - this.perPage;
                return (ceil <= 0 ? 0 : ceil) + 1;
            }
        },
        methods: {
            providerTable() {
                return [
                    { id: 1, nik: '3546789023456770', nama: 'Sale', alamat: 'Desa Klangon' },
                    { id: 2, nik: '3546789023456771', nama: 'Elas', alamat: 'Desa Klangon' },
                    { id: 3, nik: '3546789023456772', nama: 'Asel', alamat: 'Desa Klangon' },
                    { id: 4, nik: '3546789023456773', nama: 'Lesa', alamat: 'Desa Klangon' },
                    { id: 5, nik: '3546789023456774', nama: 'Sale', alamat: 'Desa Klangon' },
                    { id: 6, nik: '3546789023456775', nama: 'Elas', alamat: 'Desa Klangon' },
                    { id: 7, nik: '3546789023456776', nama: 'Asel', alamat: 'Desa Klangon' },
                    { id: 8, nik: '3546789023456777', nama: 'Lesa', alamat: 'Desa Klangon' },
                    { id: 9, nik: '3546789023456778', nama: 'Lesa', alamat: 'Desa Klangon' },
                    { id: 10, nik: '3546789023456779', nama: 'Lesa', alamat: 'Desa Klangon' },
                ]
            },
            resetPassword(id) {
                this.$refs.modalResetPassword.showModal(id);
            },
            detail(row) {
                this.$router.push({ name: 'dispenduk-almarhum-detail', params: { id: row.id } })
            },
            debounceFilter: debounce(function () {
                this.filter = this.filterDebounced
                this.currentPage = 1
            }, 500),
        },
        watch: {
            filterDebounced () {
                this.debounceFilter()
            },
        },
    }
</script>