<template>
    <b-container fluid class="web web-login p-0">
        <b-navbar class="main">
            <b-navbar-nav>
                <b-nav-item @click="$router.push('/')">
                    <div class="info-header">
                        <div class="logo">
                            <b-img :src="require('@/assets/img/logo.png')"></b-img>
                        </div>
                        <div class="description">
                            <div class="text-white">PEMERINTAH KABUPATEN BOJONEGORO</div>
                            <div class="text-white">BAGIAN KESEJAHTERAAN RAKYAT</div>
                        </div>
                    </div>
                </b-nav-item>
            </b-navbar-nav>
            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">
                <h1 class="header text-white float-right mx-auto">SI Santi</h1>
            </b-navbar-nav>
        </b-navbar>
        <!-- Header section -->
        <b-container>
            <b-row class="welcome-header login-header">
                <!-- <b-col cols="12" lg="5" class="bg-web">
                    <h1 class="header text-white">SI Santi</h1>
                    <h5 class="sub-header text-white">Sistem Informasi Santunan Kematian</h5>
                    <div class="shape">
                        <div class="big-rectangle"></div>
                        <div class="rectangle"></div>
                        <div class="big-circle"></div>
                        <div class="circle"></div>
                    </div>
                </b-col> -->
                <b-col class="bg-login bg-register" cols="12">
                    <div class="box">
                        <h5 class="header">Daftar</h5>
                        <h5>Silahkan lengkapi form untuk mendaftar</h5>
                        <b-form @submit.prevent="submitForm" class="form-horizontal mt-3">
                            <b-row>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Nik">
                                        <vue-bootstrap-typeahead 
                                            inputClass="form-control"
                                            v-model="singleRegister.nik"
                                            :data="query_nik"
                                            @hit="hitNik($event)"
                                            @input="inputNik"
                                            />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Nomor handphone">
                                        <b-form-input v-model.trim="singleRegister.no_hp"/>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12">
                                    <b-form-group label="Nama lengkap (sesuai ktp)">
                                        <b-form-input v-model.trim="singleRegister.nama"/>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12">
                                    <b-form-group label="Jenis kelamin">
                                        <b-form-radio-group id="jenis_kelamin">
                                            <b-form-radio v-model="singleRegister.jenis_kelamin" value="1">Pria</b-form-radio>
                                            <b-form-radio v-model="singleRegister.jenis_kelamin" value="0">Wanita</b-form-radio>
                                        </b-form-radio-group>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12" lg="4">
                                    <b-form-group label="Tempat lahir">
                                        <b-form-input v-model="singleRegister.tempat_lahir" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="3" lg="2">
                                    <b-form-group label="Tanggal" class="mb-0">
                                        <b-form-input maxlength="2" v-model="singleRegister.tanggal_lahir" style="padding: 0.8rem 0.75rem;" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="5" lg="3">
                                    <b-form-group label="Bulan">
                                        <b-form-select v-model="singleRegister.bulan_lahir" :options="bulanOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                                <b-col cols="4" lg="3">
                                    <b-form-group label="Tahun">
                                        <b-form-input maxlength="4" v-model="singleRegister.tahun_lahir" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" class="pb-1" offset-lg="4" lg="8"><small>Tanggal lahir</small></b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Alamat">
                                        <b-form-textarea v-model.trim="singleRegister.alamat"/>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="6" lg="4">
                                    <b-form-group label="Kecamatan">
                                        <b-form-select v-model="singleRegister.kecamatan" :options="kecamatanOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                                <b-col cols="6" lg="4">
                                    <b-form-group label="Desa/Kelurahan">
                                        <b-form-select v-model="singleRegister.desa" :options="desaOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                                <b-col cols="6" lg="2">
                                    <b-form-group label="RT" class="mb-0">
                                        <b-form-input maxlength="2" v-model="singleRegister.rt" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="6" lg="2">
                                    <b-form-group label="RW">
                                        <b-form-input maxlength="2" v-model="singleRegister.rw" />
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12" lg="4">
                                    <b-form-group label="Agama">
                                        <b-form-select v-model="singleRegister.agama" :options="agamaOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" lg="4">
                                    <b-form-group label="Jenis pekerjaan">
                                        <b-form-input v-model="singleRegister.jenis_pekerjaan" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" lg="4">
                                    <b-form-group label="Pendidikan akhir">
                                        <b-form-select v-model="singleRegister.pendidikan" :options="pendidikanOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Status kawin">
                                        <b-form-input v-model="singleRegister.status_kawin" />
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Golongan darah">
                                        <b-form-select v-model="singleRegister.golongan_darah" :options="golonganDarahOptions" class="form-control"></b-form-select>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Password" class="password">
                                        <b-form-input v-model.trim="singleRegister.password"/>
                                        <eva-icon name="eye-outline" class="show active"></eva-icon>
                                        <eva-icon name="eye-off-2-outline" class="hide"></eva-icon>
                                    </b-form-group>
                                </b-col>
                                <b-col cols="12" lg="6">
                                    <b-form-group label="Konfirmasi password" class="password">
                                        <b-form-input v-model.trim="singleRegister.konfirmasiPassword"/>
                                        <eva-icon name="eye-outline" class="show active"></eva-icon>
                                        <eva-icon name="eye-off-2-outline" class="hide"></eva-icon>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col cols="12">
                                    <center><b-button variant="primary" type="submit" class="btn-md">Mendaftar</b-button></center>
                                </b-col>
                            </b-row>
                            <b-row class="mt-3">
                                <b-col cols="12">
                                    <span class="float-right">Sudah punya akun? <router-link to="/login" class="text-success">Login disini</router-link></span>
                                </b-col>
                            </b-row>
                        </b-form>
                    </div>
                    <b-row class="mt-4">
                        <b-col cols="12">
                            <center>&copy; Bojonegoro | 2020</center>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-container>
    </b-container>
</template>

<script>
    import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
    export default {
        name: 'login',
        components: {
           VueBootstrapTypeahead 
        },
        data() {
            return {
                query_nik: ["3456789876543212", "3456789876543211", "3456789876543213", "3456789876543214"],
                singleRegister: {
                    nik: null, 
                    no_hp: null, 
                    nama: null,
                    jenis_kelamin: null,
                    tempat_lahir: null,
                    tanggal_lahir: null,
                    bulan_lahir: null,
                    tahun_lahir: null,
                    alamat: null,
                    kecamatan: null,
                    desa: null,
                    rt: null,
                    rw: null,
                    agama: null,
                    jenis_pekerjaan: null,
                    pendidikan: null,
                    status_kawin: null,
                    golongan_darah: null,
                    password: null,
                    konfirmasiPassword: null,
                },
                agamaOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Islam' },
                    { value: 2, text: 'Kristen' },
                    { value: 3, text: 'Khatolik' },
                    { value: 4, text: 'Hindu' },
                    { value: 5, text: 'Budha' },
                    { value: 6, text: 'Khonghucu' },
                ],
                kecamatanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Kecamatan Bojonegoro' },
                ],
                desaOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Desa klangon' },
                ],
                bulanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Januari' },
                    { value: 2, text: 'Februari' },
                    { value: 3, text: 'Maret' },
                    { value: 4, text: 'April' },
                    { value: 5, text: 'Mei' },
                    { value: 6, text: 'Juni' },
                    { value: 7, text: 'Juli' },
                    { value: 8, text: 'Agustus' },
                    { value: 9, text: 'September' },
                    { value: 10, text: 'Oktober' },
                    { value: 11, text: 'November' },
                    { value: 12, text: 'Desember' },
                ],
                pendidikanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'SD/MI Sederajat' },
                    { value: 2, text: 'SMP/MTs Sederajat' },
                    { value: 3, text: 'SMA/SMK/MA Sederajat' },
                    { value: 4, text: 'D1' },
                    { value: 5, text: 'D2' },
                    { value: 6, text: 'D3' },
                    { value: 7, text: 'S1/D4' },
                    { value: 8, text: 'S2' },
                    { value: 9, text: 'S3' },
                ],
                golonganDarahOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'A' },
                    { value: 2, text: 'B' },
                    { value: 3, text: 'AB' },
                    { value: 4, text: 'O' },
                ],
            }
        },
        methods: {
            submitForm () {
                this.$router.push('/login')
            },
            inputNik(item) {
                if(item === "") {
                    this.singleRegister.nik = ""
                    this.singleRegister.nama = ""
                    this.singleRegister.jenis_kelamin = ""
                    this.singleRegister.tempat_lahir = ""
                    this.singleRegister.tanggal_lahir = ""
                    this.singleRegister.bulan_lahir = ""
                    this.singleRegister.tahun_lahir = ""
                    this.singleRegister.alamat = ""
                    this.singleRegister.kecamatan = ""
                    this.singleRegister.desa = ""
                    this.singleRegister.rt = ""
                    this.singleRegister.rw = ""
                    this.singleRegister.agama = ""
                    this.singleRegister.jenis_pekerjaan = ""
                    this.singleRegister.pendidikan = ""
                    this.singleRegister.status_kawin = ""
                    this.singleRegister.golongan_darah = ""
                }
            },
            hitNik(item) {
                if(item !== null) {
                    this.singleRegister.nik = item 
                    this.singleRegister.nama = "AUTHOR"
                    this.singleRegister.jenis_kelamin = "1"
                    this.singleRegister.tempat_lahir = "BOJONEGORO"
                    this.singleRegister.tanggal_lahir = "28"
                    this.singleRegister.bulan_lahir = "2"
                    this.singleRegister.tahun_lahir = "1970"
                    this.singleRegister.alamat = "Ds klangon"
                    this.singleRegister.kecamatan = 1
                    this.singleRegister.desa = 1
                    this.singleRegister.rt = "01"
                    this.singleRegister.rw = "01"
                    this.singleRegister.agama = 1
                    this.singleRegister.jenis_pekerjaan = "PETANI"
                    this.singleRegister.pendidikan = 2
                    this.singleRegister.status_kawin = "KAWIN"
                    this.singleRegister.golongan_darah = 3
                }
            }
        },
        mounted() {
            var icon = window.$('.password .eva-hover')
            icon.click(function() {
                var element = window.$(this)
                var div = element.parent()
                if(div.find('.form-control').attr('type') == 'text') {
                    div.find('.form-control').attr('type', 'password')
                    div.find('.show').addClass('active')
                    div.find('.hide').removeClass('active')
                } else {
                    div.find('.form-control').attr('type', 'text')
                    div.find('.show').removeClass('active')
                    div.find('.hide').addClass('active')
                }
            })
        }
    }
</script>