<template>
    <div>
        <b-row>
            <b-col cols="12" lg="12">
                <div class="box">
                    <b-button @click="$router.go(-1)" class="btn btn-back"><eva-icon name="arrow-ios-back-outline"></eva-icon></b-button>
                    <b-form @submit.prevent="submitForm" class="form-horizontal">
                        <b-row>
                            <b-col cols="12" lg="3">
                                <b-form-group label="Nik">
                                    <vue-bootstrap-typeahead 
                                        inputClass="form-control"
                                        v-model="singleForm.nik"
                                        :data="query_nik"
                                        @hit="hitNik($event)"
                                        @input="inputNik"
                                        />
                                </b-form-group>
                            </b-col>
                            <b-col cols="12" lg="6">
                                <b-form-group label="Nama lengkap (sesuai ktp)">
                                    <b-form-input v-model.trim="singleForm.nama"/>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12">
                                <b-form-group label="Jenis kelamin">
                                    <b-form-radio-group id="jenis_kelamin">
                                        <b-form-radio v-model="singleForm.jenis_kelamin" value="1">Pria</b-form-radio>
                                        <b-form-radio v-model="singleForm.jenis_kelamin" value="0">Wanita</b-form-radio>
                                    </b-form-radio-group>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" lg="4">
                                <b-form-group label="Tempat lahir">
                                    <b-form-input v-model="singleForm.tempat_lahir" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="3" lg="1">
                                <b-form-group label="Tanggal" class="mb-0">
                                    <b-form-input maxlength="2" v-model="singleForm.tanggal_lahir" style="padding: 0.8rem 0.75rem;" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="5" lg="3">
                                <b-form-group label="Bulan">
                                    <b-form-select v-model="singleForm.bulan_lahir" :options="bulanOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4" lg="2">
                                <b-form-group label="Tahun">
                                    <b-form-input maxlength="4" v-model="singleForm.tahun_lahir" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="12" class="pb-1" offset-lg="4" lg="8"><small>Tanggal lahir</small></b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" lg="6">
                                <b-form-group label="Alamat">
                                    <b-form-textarea v-model.trim="singleForm.alamat"/>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="6" lg="4">
                                <b-form-group label="Kecamatan">
                                    <b-form-select v-model="singleForm.kecamatan" :options="kecamatanOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                            <b-col cols="6" lg="4">
                                <b-form-group label="Desa/Kelurahan">
                                    <b-form-select v-model="singleForm.desa" :options="desaOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                            <b-col cols="6" lg="1">
                                <b-form-group label="RT" class="mb-0">
                                    <b-form-input maxlength="2" v-model="singleForm.rt" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="6" lg="1">
                                <b-form-group label="RW">
                                    <b-form-input maxlength="2" v-model="singleForm.rw" />
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" lg="3">
                                <b-form-group label="Agama">
                                    <b-form-select v-model="singleForm.agama" :options="agamaOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                            <b-col cols="12" lg="4">
                                <b-form-group label="Jenis pekerjaan">
                                    <b-form-input v-model="singleForm.jenis_pekerjaan" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="12" lg="4">
                                <b-form-group label="Pendidikan akhir">
                                    <b-form-select v-model="singleForm.pendidikan" :options="pendidikanOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" lg="4">
                                <b-form-group label="Status kawin">
                                    <b-form-input v-model="singleForm.status_kawin" />
                                </b-form-group>
                            </b-col>
                            <b-col cols="12" lg="2">
                                <b-form-group label="Golongan darah">
                                    <b-form-select v-model="singleForm.golongan_darah" :options="golonganDarahOptions" class="form-control"></b-form-select>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" lg="2">
                                <b-form-file
                                    class="picture"
                                    placeholder="Foto rumah"
                                    v-model="singleForm.fotoRumah"
                                ></b-form-file>
                            </b-col>
                            <b-col cols="12" class="mt-3">
                                <span>*) Klik icon untuk mengupload file anda</span>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col cols="12" class="mt-3">
                                <b-button variant="primary" type="submit" class="float-right">Simpan</b-button>
                            </b-col>
                        </b-row>
                    </b-form>
                </div>
            </b-col>
        </b-row>
    </div>
</template>
<script>
    import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
    export default {
        name: 'dtks-almarhum-tambah',
        components: {
           VueBootstrapTypeahead 
        },
        data() {
            return {
                query_nik: ["3456789876543212", "3456789876543211", "3456789876543213", "3456789876543214"],
                singleForm: {
                    nik: "",  
                    nama: null,
                    jenis_kelamin: null,
                    tempat_lahir: null,
                    tanggal_lahir: null,
                    bulan_lahir: null,
                    tahun_lahir: null,
                    alamat: null,
                    kecamatan: null,
                    desa: null,
                    rt: null,
                    rw: null,
                    agama: null,
                    jenis_pekerjaan: null,
                    pendidikan: null,
                    status_kawin: null,
                    golongan_darah: null,
                    fotoRumah: null,
                },
                agamaOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Islam' },
                    { value: 2, text: 'Kristen' },
                    { value: 3, text: 'Khatolik' },
                    { value: 4, text: 'Hindu' },
                    { value: 5, text: 'Budha' },
                    { value: 6, text: 'Khonghucu' },
                ],
                kecamatanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Kecamatan Bojonegoro' },
                ],
                desaOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Desa klangon' },
                ],
                bulanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'Januari' },
                    { value: 2, text: 'Februari' },
                    { value: 3, text: 'Maret' },
                    { value: 4, text: 'April' },
                    { value: 5, text: 'Mei' },
                    { value: 6, text: 'Juni' },
                    { value: 7, text: 'Juli' },
                    { value: 8, text: 'Agustus' },
                    { value: 9, text: 'September' },
                    { value: 10, text: 'Oktober' },
                    { value: 11, text: 'November' },
                    { value: 12, text: 'Desember' },
                ],
                pendidikanOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'SD/MI Sederajat' },
                    { value: 2, text: 'SMP/MTs Sederajat' },
                    { value: 3, text: 'SMA/SMK/MA Sederajat' },
                    { value: 4, text: 'D1' },
                    { value: 5, text: 'D2' },
                    { value: 6, text: 'D3' },
                    { value: 7, text: 'S1/D4' },
                    { value: 8, text: 'S2' },
                    { value: 9, text: 'S3' },
                ],
                golonganDarahOptions: [
                    { value: null, text: '- Pilih -' },
                    { value: 1, text: 'A' },
                    { value: 2, text: 'B' },
                    { value: 3, text: 'AB' },
                    { value: 4, text: 'O' },
                ],
            }
        },
        methods: {
            submitForm() {
                this.$noty.success('Data almarhum berhasil di simpan');
                this.$router.push({ name: 'dtks-almarhum' })
            },
            inputNik(item) {
                if(item === "") {
                    this.singleForm.nik = ""
                    this.singleForm.nama = ""
                    this.singleForm.jenis_kelamin = ""
                    this.singleForm.tempat_lahir = ""
                    this.singleForm.tanggal_lahir = ""
                    this.singleForm.bulan_lahir = ""
                    this.singleForm.tahun_lahir = ""
                    this.singleForm.alamat = ""
                    this.singleForm.kecamatan = ""
                    this.singleForm.desa = ""
                    this.singleForm.rt = ""
                    this.singleForm.rw = ""
                    this.singleForm.agama = ""
                    this.singleForm.jenis_pekerjaan = ""
                    this.singleForm.pendidikan = ""
                    this.singleForm.status_kawin = ""
                    this.singleForm.golongan_darah = ""
                }
            },
            hitNik(item) {
                if(item !== null) {
                    this.singleForm.nik = item 
                    this.singleForm.nama = "ASLE"
                    this.singleForm.jenis_kelamin = "1"
                    this.singleForm.tempat_lahir = "BOJONEGORO"
                    this.singleForm.tanggal_lahir = "28"
                    this.singleForm.bulan_lahir = "2"
                    this.singleForm.tahun_lahir = "1970"
                    this.singleForm.alamat = "Ds klangon"
                    this.singleForm.kecamatan = 1
                    this.singleForm.desa = 1
                    this.singleForm.rt = "01"
                    this.singleForm.rw = "01"
                    this.singleForm.agama = 1
                    this.singleForm.jenis_pekerjaan = "PETANI"
                    this.singleForm.pendidikan = 2
                    this.singleForm.status_kawin = "KAWIN"
                    this.singleForm.golongan_darah = 3
                }
            }
        }
    }
</script>